ion-content.login-content {
  
  .container {
    position: relative;
    width: 100%;
    min-height: 100vh;
    background-color: #fff;
    overflow: hidden;
  }
  
  .container:before {
    content: "";
    position: absolute;
    width: 2000px;
    height: 2000px;
    border-radius: 50%;
    background: linear-gradient(-45deg, #4481eb, #04befe);
    top: -10%;
    right: 48%;
    transform: translateY(-50%);
    z-index: 6;
    transition: 1.8s ease-in-out;
  }
  
  .form-container {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
  }
  
  .signin-signup {
    position: absolute;
    top: 50%;
    left: 70%;
    transform: translate(-50%, -50%);
    width: 50%;
    display: grid;
    grid-template-columns: 1fr;
    z-index: 5;
    transition: 1s 0.7s ease-in-out;
  }
  
  form {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    grid-column: 1 / 2;
    grid-row: 1 / 2;
    padding: 0 5rem;
    overflow: hidden;
    transition: 0.2s 0.7s ease-in-out;
  }
  
  form.sign-in-form {
    z-index: 2;
  }
  
  form.sign-up-form {
    z-index: 1;
    opacity: 0;
  }
  
  h1 {
    font-family: "Poppins";
    font-weight: 600 !important;
    font-size: 2.2rem !important;
    color: #444;
    // margin-bottom: 10px;
  }
  
  ion-item {
    margin-top: 14px;
    --background: #f0f0f0;
    --border-radius: 55px;
  }
  
  ion-input {
    margin-bottom: 4px;
    font-weight: 600;
    font-size: 1.1rem;
    color: #333;
    --placeholder-opacity: 0.3 !important;
    --placeholder-font-weight: 400 !important;
  }
  
  ion-text {
    font-weight: 600;
  }
  
  ion-button {
    width: 150px;
    height: 49px;
    outline: none;
    --border-radius: 49px;
    cursor: pointer;
    --background: #5995fd !important;
    color: #fff;
    text-transform: uppercase;
    font-weight: 600;
    margin: 10px 0;
    --transition: 0.5s !important;
    --color-hover: #4d84e2 !important;
  }
  
  .sc-iyvyFf {
    position: relative !important;
  }
  
  .social-text {
    padding: 0.7rem 0;
    font-size: 1rem;
  }
  
  .social-media {
    display: flex;
    justify-content: center;
  
    .social-icon {
      height: 46px;
      width: 46px;
      border: 1px solid #333;
      margin: 0 0.45rem;
      display: flex;
      justify-content: center;
      align-items: center;
      text-decoration: none;
      color: #333;
      font-size: 1.1rem;
      border-radius: 50%;
      transition: 0.3s;
    }
  
    .social-icon:hover {
      background-color: #4481eb;
      border-color: #4481eb;
    }
  }
  
  .panels-container {
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
  
  .panel {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: space-around;
    text-align: center;
    z-index: 7;
  
    .content {
      color: #fff;
      transition: 0.9s 0.6s ease-in-out;
    }
  
    h3 {
      font-weight: 600;
      line-height: 1;
      font-size: 1.5rem;
    }
  
    p {
      font-size: 0.95rem;
      padding: 0.7rem 0;
    }
  }
  
  .btn {
    width: 150px;
    background-color: #5995fd;
    border: none;
    outline: none;
    height: 49px;
    border-radius: 49px;
    color: #fff;
    text-transform: uppercase;
    font-weight: 600;
    margin: 10px 0;
    cursor: pointer;
    transition: 0.5s;
  }
  
  .btn:hover {
    background-color: #4d84e2;
  }
  
  .btn.transparent {
    margin: 0;
    background: 0;
    border: 2px solid #fff;
    width: 130px;
    height: 43px;
    font-weight: 600;
    font-size: 0.8;
  }
  
  .left-panel {
    pointer-events: all;
    padding: 3rem 17% 2rem 12%;
  }
  
  .right-panel {
    pointer-events: none;
    padding: 3rem 12% 2rem 17%;
  }
  
  .image {
    margin-top: 15px;
    width: 100%;
    transition: 1.1s 0.4s ease-in-out;
  }
  
  .right-panel .content,
  .right-panel .image {
    transform: translateX(800px);
  }
  
  .container.sign-up-mode:before {
    transform: translate(100%, -50%);
    right: 52%;
  }
  
  .container.sign-up-mode .left-panel .image,
  .container.sign-up-mode .left-panel .content {
    transform: translateX(-800px);
  }
  
  .container.sign-up-mode .right-panel .content,
  .container.sign-up-mode .right-panel .image {
    transform: translateX(0px);
  }
  
  .container.sign-up-mode .left-panel {
    pointer-events: none;
  }
  
  .container.sign-up-mode .right-panel {
    pointer-events: all;
  }
  
  .container.sign-up-mode .signin-signup {
    left: 25%;
  }
  
  .container.sign-up-mode form.sign-in-form {
    z-index: 1;
    opacity: 0;
  }
  
  .container.sign-up-mode form.sign-up-form {
    z-index: 2;
    opacity: 1;
  }
  
  @media (max-width: 870px) {
    .container {
      min-height: 800px;
      height: 100vh;
    }
  
    .container:before {
      width: 1500px;
      height: 1500px;
      left: 30%;
      bottom: 68%;
      transform: translateX(-50%);
      right: initial;
      top: initial;
      transition: 2s ease-in-out;
    }
  
    .signin-signup {
      width: 100%;
      left: 50%;
      top: 80%;
      transform: translate(-50%, -100%);
      transition: 1s 0.8s ease-in-out;
    }
  
    .panels-container {
      grid-template-columns: 1fr;
      grid-template-rows: 1fr 2fr 1fr;
    }
  
    .panel {
      flex-direction: row;
      justify-content: space-around;
      align-items: center;
      padding: 2.5rem 8%;
  
      .content {
        padding-right: 15%;
        transition: 0.9s 0.8s ease-in-out;
      }
  
      h3 {
        font-size: 1.2rem;
      }
  
      p {
        font-size: 0.7rem;
        padding: 0.5rem 0;
      }
    }
  
    .btn.transparent {
      width: 110px;
      height: 35px;
      font-size: 0.7rem;
    }
  
    .image {
      width: 200px;
      transition: 0.9s 0.6s ease-in-out;
    }
  
    .left-panel {
      grid-row: 1 / 2;
    }
  
    .right-panel {
      grid-row: 3 / 4;
    }
  
    .right-panel .content,
    .right-panel .image {
      transform: translateY(300px);
    }
  
    .container.sign-up-mode:before {
      transform: translate(-50%, 100%);
      bottom: 32%;
      right: initial;
    }
  
    .container.sign-up-mode .left-panel .image,
    .container.sign-up-mode .left-panel .content {
      transform: translateY(-300px);
    }
  
    .container.sign-up-mode .right-panel .image,
    .container.sign-up-mode .right-panel .content {
      transform: translateY(-50px);
    }
  
    .container.sign-up-mode .signin-signup {
      top: 5%;
      transform: translate(-50%, 0);
      left: 50%;
    }
  }
  
  @media (max-width: 570px) {
    form {
      padding: 0 1.5rem;
    }
  
    .image {
      display: none;
    }
    .panel .content {
      padding: 0.5rem 1rem;
    }
    .container {
      padding: 1.5rem;
    }
  
    .container:before {
      bottom: 72%;
      left: 50%;
    }
  
    .container.sign-up-mode:before {
      bottom: 30%;
      left: 50%;
    }
  }
}

